@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Indie Flower', cursive;
  background-image: linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
}

.correct {
  background-color: #4de167 !important;
}

.incorrect {
  background-color: #d52929 !important;
}

.quiz-container {
  background-color: #007f7f;
  padding: 2rem;
  min-width: 25%;
  margin-bottom: 2rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.quiz-question__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.quiz-top {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__heading {
    font-size: 3.5rem;
  }

  &__count {
    color: white;
    margin-bottom: 1.2rem;
  }

  &__display {
    padding: 2rem;
    background-color: #fff;
    border-radius: 10px;
    width: 100%;
    margin-bottom: 2rem;
    font-size: 2.5rem;
  }
}

.quiz-question {
  display: flex;
  align-items: center;
  gap: 2rem;

  &__number {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    height: 70px;
    width: 70px;
    background-color: white;
    border-radius: 50%;
  }

  &__answer {
    font-size: 2rem;
    background-color: white;
    padding: 2rem;
    cursor: pointer;
    flex: 1;
  }
}

.controls {
  &__btn {
    padding: 1.4rem 2.4rem;
    border: none;
    background: hsla(180, 96%, 38%, 1);
    transition: all 200ms ease;
    cursor: pointer;

    &:hover {
      background: hsla(180, 96%, 38%, 0.5);
    }
  }
}
